<template>
  <div class="register">
    <div class="login clearfix">
      <div class="login-text fr" @click="goLogin">注册</div>
    </div>
    <div class="logo">
      <img src="../assets/images/open.png" alt class="auto-img logo-img" />
    </div>
    <div class="register-form">
      <van-form>
        <van-field
          v-model="userRegisterInfo.phone"
          placeholder="请输入手机号"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="userRegisterInfo.password"
          :type="ispassword ? 'password' : 'text'"
          placeholder="请输入密码"
          :rules="[{ required: true, message: '请填写密码' }]"
          :right-icon="ispassword ? 'closed-eye' : 'eye-o'"
          @click-right-icon="passwordShow"
        />
        <div style="margin: 25px 16px">
          <van-button
            round
            block
            type="info"
            native-type="submit"
            @click="submitRegister"
            >登 录</van-button
          >
          <van-button
            round
            block
            type="info"
            native-type="submit"
            @click="backRegister"
            >取 消 登 录</van-button
          >
        </div>
      </van-form>
    </div>
    <div class="register-type">
      <van-divider
        :style="{ color: '#8a8a8a', borderColor: '#00b46d', padding: '0 16px' }"
        >其他登录方式</van-divider
      >
      <div class="type-img clearfix">
        <div class="type-item fl">
          <div class="iconimg">
            <img src="../assets/images/QQ.png" alt class="auto-img" />
          </div>
        </div>
        <div class="type-item fl">
          <div class="iconimg">
            <img src="../assets/images/weixin.png" alt class="auto-img" />
          </div>
        </div>
        <div class="type-item fl">
          <div class="iconimg">
            <img src="../assets/images/weibo.png" alt class="auto-img" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { validForm } from "../assets/js/validForm";
export default {
  data() {
    return {
      // 密码显示
      ispassword: true,
      // 用户登录信息
      userRegisterInfo: {
        phone: "",
        password: "",
      },
    };
  },
  methods: {
    //注册
    goLogin() {
      this.$router.push({ name: "Login" });
    },
    //退出登录
    backRegister() {
      this.$router.push({ name: "Home" });
    },

    // 密码显示隐藏
    passwordShow() {
      this.ispassword = !this.ispassword;
    },

    submitRegister() {
      let userInfo = this.userRegisterInfo;

      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机格式不正确",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
      };

      //验证表单
      if (!validForm.valid(o)) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0,
      });

      setTimeout(() => {
        this.$toast.clear();
        let lay = JSON.parse(localStorage.getItem("Login"));
        // 是否被注册过
        let isRegister = lay.some((v) => {
          return (
            v.phone == this.userRegisterInfo.phone &&
            v.password == this.userRegisterInfo.password
          );
        });
        // 注册成功，可以登录
        if (isRegister) {
          let token = {
            phone: this.userRegisterInfo.phone,
            password: this.userRegisterInfo.password,
          };
          // 添加昵称
          let layarr = JSON.parse(localStorage.getItem("Login"));
          for (let i = 0; i < layarr.length; i++) {
            if (
              layarr[i].phone == token.phone &&
              layarr[i].password == token.password
            ) {
              token.niakName = layarr[i].nickName;
            }
          }
          // 保存登录信息，跳转到home
          localStorage.setItem("token", JSON.stringify(token));
          this.$toast.success("登录成功");
          this.$router.push({ name: "Home" });
        } else {
          this.$toast.fail("登录失败，密码错误");
        }
        // 清空注册表单的数据
        for (let key in userInfo) {
          userInfo[key] = "";
        }
      }, 1000);
    },
  },
};
</script>

<style lang="less" scoped>
.register {
  width: 100%;
  height: 100%;
  background: url("../assets/images/bg.png") no-repeat;
  background-size: 100% 100%;

  .login {
    padding: 15px 10px;
  }

  .login-text {
    font-size: 14px;
    color: #fff;
  }

  .logo {
    text-align: center;
    height: 120px;
    width: 120px;
    margin: 50px auto 10px;
    border-radius: 50%;
    overflow: hidden;
  }

  .register-form {
    padding-top: 15px;
  }

  /deep/ .van-cell {
    background: none;
  }

  /deep/ .van-cell::after {
    border-bottom: 1px solid #00b46d;
  }

  /deep/ .van-cell.van-field {
    padding: 15px 50px;
  }

  .type-img {
    padding: 0 20px;
    display: flex;
  }

  .type-item {
    height: 40px;
    flex: 1;
  }

  .iconimg {
    width: 30px;
    height: 30px;
    margin: 0 auto;
    border-radius: 50%;
  }

  /deep/ .van-field__control {
    color: #fff;
  }

  /deep/ .van-button--round {
    margin-bottom: 15px;
  }

  /deep/ .van-button--info {
    background-color: #00b46d;
    border: 0.02667rem solid #00b46d;
  }
}
</style>